import Header from "./components/header";
import Link from "next/link";
import Image from "next/image";
import fb from "../public/graphics/facebook.png";
import ig from "../public/graphics/instagram.png";
import yt from "../public/graphics/youtube.png";

export default function Home() {
  return (
    <div>
      
      <section className="bg-bannerImg bg-repeat bg-cover bg-bottom w-full h-screen">
        <Header />
        <div className="flex flex-col justify-center items-center w-full h-screen bg-blackOverlay">
          <h1 className="text-white text-6xl">The 756</h1>
          <p className="text-white text-2xl">Shaping the future of music with boundless creativity and unstoppable energy.</p>
        
          <div className="absolute flex justify-between w-1/6 bottom-0 left-0 p-16">
            <Link rel="stylesheet" href="/">
              <Image src={fb} alt="Facebook" width={32} height={32} />
            </Link>
            <Link rel="stylesheet" href="/">
              <Image src={ig} alt="Instagram" width={32} height={32} />
            </Link>
          </div>
        </div>
        
      </section>
      
      <section className="w-full h-screen bg-background">

      </section>
      
    </div>
  );
}
