import Link from "next/link";
import Image from "next/image";
import logo from '../../public/graphics/756-Logo.png';

export default function Header() {
    return (
    <header className="fixed w-full text-white">
        <div className="flex justify-between items-center p-16">
            <div>
                <Link href="/">
                    <Image src={logo} alt="Logo" width={50} height={50} />
                </Link>
            </div>
            <nav className="w-1/3 flex justify-between p-2">
            <div>
            <Link href="/">
                Home
            </Link>
            </div>
            <div>
            <Link href="/about">
                About
            </Link>
            </div>
            <div>
            <Link href="/portfolio">
                Portfolio
            </Link>
            </div>
            <div>
            <Link href="/services">
                Services
            </Link>
            </div>
            <div>
            <Link href="/contact">
                Contact
            </Link>
            </div>
        </nav>
            <div>
                <Link href="" className="border-2 border-white p-4 rounded-full">
                    Work With Us
                </Link>
            </div>
        </div>
    </header>
    );
} 
 //The  Header  component is a simple navigation bar that contains links to the Home, About, Services, and Contact pages. 
 //The  Link  component from Next.js is used to create links to the different pages. 
 //The  Header  component is then imported into the  Layout  component.